.addForm {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px 20px;
  gap: 50px;
  border-bottom: 1px solid #d9d9d9;
}
.addForm .uploudButton {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  height: 30xp;
  padding: 14px;
  border: 1px solid #d9d9d9;
  border-radius: 8px;
  transition: all 0.5s ease;
  width: 140px;

  &:hover {
    border: 1px solid #a5a5a5;
    transition: all 0.5s ease;
  }
}
