  // flex-direction: column;
  // justify-content: space-between;

  font-family: "Segoe UI", sans-serif;
  font-weight: 300;
  // font-size: 18px;

  @media (min-width: 1500px) {
    // flex-direction: row;
  }
  @media (max-width: 888px) {
    // margin-bottom: 30px;
  }
}

.prices-contianer__title {
  // font-family: "SFMono";

  // font-weight: 300;
}

.prices-contianer__text {
  font-size: 18px;
}

.column__list-item {
  font-family: "Inter";
  font-size: 16px;
  font-weight: 300;
  // text-align: right;

  @media (max-width: 620px) {
    text-align: right;
  }
}

//footer
.footer-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.footer-wrapper-prices {
  display: flex;
  flex-direction: column;
}

