.contacts-container {
  margin: 90px 40px 20px 40px;
  padding: 50px 0;

  border-top: 1px solid;
  border-bottom: 1px solid;

  font-family: "Segoe UI", sans-serif;
  font-weight: 300;
  font-size: 18px;

  min-height: 40vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  text-align: center;

  @media (max-width: 800px) {
    margin: 70px 10px 20px 10px;
  }
  @media (max-width: 520px) {
    margin: 80px 10px 20px 10px;
  }
}

.contacts-button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;

  @media (max-width: 500px) {
    flex-direction: column;
  }
}

.contacts-button__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 8px;
  border: 1px solid;
  border-radius: 8px;
  transition: all 0.5s ease;

  font-family: "Segoe UI", sans-serif;
  font-weight: 300;

  &:hover {
    background-color: rgba($color: #c0c0c0, $alpha: 0.5);

    transition: all 0.5s ease;
  }
}
