.header-modal.ant-modal-content {
  height: 100vh !important;
  border-radius: 0px !important;
  box-shadow: none !important;
  padding: 0 !important;
  background: linear-gradient(
    135deg,
    rgb(238, 238, 238) 50%,
    rgb(193, 193, 193) 100%
  ) !important;
}

.mobile-menu {
