.form-wrapper {
  display: flex;
  gap: 10px;
}

.preview-wrapper {
  border: 1px solid #d9d9d9;
  padding: 10px;
  margin: 10px 0;
  border-radius: 8px;

  h3 {
    font-weight: 700;
    margin: 10px 0px;
  }
}

.button-wrapper {
  display: flex;
  justify-content: end;
  width: 100%;
}

// .ant-upload-list-item-container {
//   width: 50px;
//   height: 50px;
// }

.preview-wrapper .ant-upload-list-item-image {
  object-fit: cover !important;
  border-radius: 6px !important;
}

.preview-wrapper .ant-upload-list-item-thumbnail {
  width: 100px !important;
  height: 100px !important;
}

.preview-wrapper .ant-upload-list-item {
  height: 115px !important;
}

//Для табов
.ant-tabs-editable > .ant-tabs-nav .ant-tabs-tab {
  transition: none;
}

.resp-wrapper {
  margin: 30px 5px;
  border: 1px solid #d9d9d9;
  padding: 10px;
  margin: 10px 0;
  border-radius: 8px;
  h3 {
    font-weight: 700;
    margin: 10px 0px;
  }
}
.resp {
  display: flex;
  align-items: center;
  gap: 10px;
}
.resp_link {
  display: flex;

  align-items: center;
  height: 30px;
  margin-left: 10px;

  svg {
    display: block;
  }
}
.resp__button {
  margin-left: 10px;
  padding: 5px;
  font-size: 20px;
}

.ant-tabs-nav::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px; /* Толщина линии */
  bottom: 0;
  left: 0;
  background: linear-gradient(90deg, transparent, #fff, transparent);
  border-bottom: none !important; /* Убираем стандартную линию */
}

.zip-wrapper {
  border: 1px solid #d9d9d9;
  padding: 10px;
  margin: 10px 0;
  border-radius: 8px;

  h3 {
    font-weight: 700;
    margin: 10px 0px;
  }
}
.children-wrapper {
  border: 1px solid #d9d9d9;
  padding: 10px;
  margin: 10px 0;
  border-radius: 8px;

  position: relative;

  h3 {
    font-weight: 700;
    margin: 10px 0px;
  }
}
